import { useEffect, useRef, useState } from "react";
import { Menu, InputNumber, Button, Drawer, Slider, Radio } from "antd";
import { MenuUnfoldOutlined } from "@ant-design/icons";
import { useHover } from "../../utils";
import { IBaseProps } from "../../types";
import { animated, config, useSpring } from "react-spring/web";
import './console.scss'

const { Item, SubMenu } = Menu;

interface IConsoleProps extends IBaseProps {
    /**  */
    operation?: React.ReactNode;
    /**  */
    displayer?: React.ReactNode;
    /** drawer */
    drawerHeight?: number;
    /** silider */
    showSilider?: boolean;
    /**  */
    isUpdate?: boolean;
    /**  */
    isIndex?: boolean;
    /** value */
    valueRange?: [number, number];
    /** index */
    indexRange?: [number, number];
    /** value */
    defaultValue?: number;
    /** index */
    defaultIndex?: number;
    /**  */
    addText?: string;
    /**  */
    deleteText?: string;
    /** slider */
    onSliderChange?: (value: number) => void;
    /** value */
    onValueChange?: (value: number) => void;
    /** index */
    onIndexChange?: (index: number) => void;
    /**  */
    onAdd?: (value: number, index: number) => void;
    /**  */
    onDelete?: (index: number) => void;
}

const Console: React.FC<IConsoleProps> = (props) => {

    const {
        children,
        style,
        operation,
        displayer,
        drawerHeight,
        showSilider,
        addText,
        defaultIndex,
        valueRange,
        indexRange,
        defaultValue,
        deleteText,
        isUpdate,
        isIndex,
        onSliderChange,
        onAdd,
        onDelete,
        onIndexChange,
        onValueChange
    } = props;

    const [hoverRef, isHover] = useHover();
    const [isUnfold, setIsUnfold] = useState(false);
    /** valueindex */
    const [value, setValue] = useState(defaultValue || 0);
    const [index, setIndex] = useState(defaultIndex || 0);

    //  radio
    const [radioActived, setRadioActived] = useState(1);

    const displayConRef = useRef<HTMLDivElement>();
    const { opacity } = useSpring({
        opacity: isHover ? 0.7 : 0.2,
        config: config.gentle
    })

    /**  displayer  */
    useEffect(() => {
        if (displayConRef.current) displayConRef.current.scrollTop = displayConRef.current.scrollHeight;
    }, [displayConRef.current?.scrollHeight])

    return (
        <animated.div
            className='console-warp'
            ref={hoverRef as any}
            style={{ ...style, opacity }}
        >
            <Menu
                className='console'
                mode="inline"
                theme="dark"
                inlineCollapsed={true}
                selectable={false}
                style={{ display: isUnfold ? 'none' : 'inline-block' }}
            >
                <Item
                    icon={<MenuUnfoldOutlined />}
                    key='item0'
                    onClick={() => {
                        setIsUnfold(true);
                    }}
                >
                    
                </Item>
                {children}
            </Menu>
            <Drawer
                className='console-drawer'
                title=''
                height={drawerHeight}
                visible={isUnfold}
                placement='bottom'
                mask={false}
                onClose={() => { setIsUnfold(false) }}
            >

                <div className='operation'>
                    {showSilider &&
                        <div className='slider-warp'>
                            
                            <Slider
                                className='slider'
                                defaultValue={32}
                                onChange={(value: number) => onSliderChange?.(value)}
                            />
                        </div>
                    }

                    {/*  */}
                    {operation}

                    {/*  */}
                    {
                        isUpdate &&
                        <div className='input-group'>
                            <Radio.Group
                                className='radio-group'
                                defaultValue={1}
                                onChange={(e) => {
                                    setRadioActived(e.target.value);
                                }}
                            >
                                <Radio value={1}>{addText}</Radio>
                                <Radio value={2}>{deleteText}</Radio>
                            </Radio.Group>

                            <div className='label-group'>
                                {
                                    radioActived === 1 &&
                                    <label>
                                        <span className='label-name'>:</span>
                                        <InputNumber
                                            min={valueRange?.[0]}
                                            max={valueRange?.[1]}
                                            defaultValue={defaultValue}
                                            onChange={(value) => {
                                                setValue(value as number)
                                                onValueChange?.(value);
                                            }}
                                        />
                                    </label>
                                }
                                {
                                    isIndex &&
                                    <label>
                                        <span className='label-name'>:</span>
                                        <InputNumber
                                            min={indexRange?.[0]}
                                            max={valueRange?.[1]}
                                            defaultValue={defaultIndex}
                                            onChange={(index) => {
                                                setIndex(index as number)
                                            }}
                                        />
                                    </label>
                                }

                                {radioActived === 1 && <Button type='primary' onClick={() => onAdd?.(value, index)}>{addText}</Button>}
                                {radioActived === 2 && <Button type='primary' onClick={() => onDelete?.(index)}>{deleteText}</Button>}
                            </div>
                        </div>
                    }
                </div>

                <div className='displayer'>
                    <div className='content' ref={displayConRef as any}>
                        {displayer}
                    </div>
                </div>
            </Drawer>
        </animated.div>
    )
}

Console.defaultProps = {
    showSilider: true,
    addText: '',
    deleteText: '',
    defaultIndex: 2,
    defaultValue: 3,
    valueRange: [3, 90],
    indexRange: [0, 10],
    isUpdate: true,
    isIndex: true
}

export { Item, SubMenu };
export default Console;




